<template>
  <div class="pageContent">
    <el-card class="filter-container" shadow="never">
      <div class="pageHead flex-row-between">
        <div>账户总览</div>
      </div>
      <el-row :gutter="12">
        <el-col :span="8">
          <el-card shadow="hover">
            <div class="flex-row-between">
              <div class="flex-row-start">
                <div class="cardHead">可用店铺余额(元)</div>
                <el-tooltip class="item" effect="dark" 
                  content="可用店铺余额=店铺余额-不可用店铺余额；交易订单结算完成的入账金额（含充值成功资金），可提现；" 
                  placement="top-start">
                  <i class="el-icon-question ii"></i>
                </el-tooltip>
              </div>
              <!-- <el-button type="text" @click="$router.push({path:'/assetsManage/capitalManage/billDetail'})">明细</el-button> -->
            </div>
            <div class="cardContent flex-row-between">
              <div class="warningFont wfs">{{form.shopAsset}}</div>
              <div class="flex-row-start">
                <el-button type="primary" plain>充值</el-button>
                <el-button type="success" plain>提现</el-button>
              </div>
            </div>
          </el-card>
        </el-col>
        <el-col :span="8">
          <el-card shadow="hover">
            <div class="flex-row-between">
              <div class="flex-row-start">
                <div class="cardHead">待结算</div>
                <el-tooltip class="item" effect="dark" 
                  content="订单从付款成功（不含储值余额）到结算完成期间的订单总成交金额（未扣除手续费）" 
                  placement="top">
                  <i class="el-icon-question ii"></i>
                </el-tooltip>
              </div>
              <el-button type="text" @click="$router.push({path:'/assetsManage/account/record'})">明细</el-button>
            </div>
            <div class="cardContent warningFont wfs">{{form.unSettleAsset}}</div>
          </el-card>
        </el-col>
        <el-col :span="8">
          <el-card style="height:140px;" shadow="hover">
            <div class="flex-row-between">
              <div>账户信息</div>
              <el-button type="text" @click="$router.push({path:'/shop/shopAttestation'})">去认证</el-button>
            </div>
            <div class="flex-row-between">
              <div class="b6">
                <div>商户名：{{shop.shopName}}</div>
                <div>银行卡：1张</div>
              </div>
              <div class="b4 flex-row-around">
                <div class="curP" @click="$router.push({path:'/shop/shopAttestation'})">
                  <img style="margin:0 auto;width:25px;height:25px;" src="../../../assets/images/assetsManage/rz.png" alt="">
                  <span class="f12 infoFont" style="margin-top:4px;">认证信息</span>
                </div>
                <div class="curP" @click="$router.push({path:'/moneyConfig/payConfig'})">
                  <img style="margin:0 auto;width:25px;height:25px;" src="../../../assets/images/assetsManage/zf.png" alt="">
                  <span class="f12 infoFont" style="margin-top:4px;">支付方式</span>
                </div>
              </div>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </el-card>
    <!-- 数据列表 - 筛选-->
    <el-card class="operate-container mt10" shadow="never">
      <div class="pageHead flex-row-between">
        <div class="flex-row-start">
          <div style="margin-right:4px;">收支情况</div>
          <el-tooltip class="item" effect="dark" content="对近期各个账户收入和支出的数据统计" placement="top">
            <i class="el-icon-question"></i>
          </el-tooltip>
        </div>
        <el-button type="text" @click="$router.push({path:'/assetsManage/capitalManage/bill'})">查看更多</el-button>
      </div>
      <el-table ref="multipleTable" :data="list" tooltip-effect="dark" style="width: 100%">
        <el-table-column label="日期" width="200" prop="balanceDay"></el-table-column>
        <el-table-column label="账户" width="140">
          <template slot-scope="scope">
            <div :data-id="scope.row.id">店铺余额</div>
          </template>
        </el-table-column>
        <el-table-column label="期初余额(元)" width="140" prop="lastBalanceAmount"></el-table-column>
        <el-table-column label="收入(元)" width="140" prop="incomeAmount"></el-table-column>
        <el-table-column label="收入笔数" width="140" prop="incomeCount"></el-table-column>
        <el-table-column label="支出(元)" width="140" prop="expenditureAmount"></el-table-column>
        <el-table-column label="支出笔数" width="140" prop="expenditureCount"></el-table-column>
        <el-table-column label="结余(元)" width="140" prop="thisBalanceAmount"></el-table-column>
      </el-table>
      <div class="flex-row-end" style="padding-top:20px;">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[5,10,15,20]"
          :page-size="searchList.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="listTotal">
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getShopAssetDayPage } from '@/api/assetsManage/capitalManage'
import { getShopAsset } from '@/api/assetsManage/verifyFund'
export default {
  data(){
    return{
      searchList:{
        pageSize:5,
        pageNum:1
      },
      pickerOptions: {
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime());
              end.setHours(end.getHours());
              end.setMinutes(end.getMinutes());
              end.setSeconds(end.getSeconds());
              const start = new Date();
              start.setTime(start.getTime());
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime()- 3600 * 1000 * 24);
              end.setHours(23);
              end.setMinutes(59);
              end.setSeconds(59);
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24);
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
        ]
      },
      list:[],
      listTotal:0,
      form:{}
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.getData()
    this.getMoney()
  },
  methods:{
    getData(){
      getShopAssetDayPage(this.searchList).then(res=>{
        console.log('日账单列表',res)
        if(res.code == 200){
          this.list = res.data.list;
          this.listTotal = res.data.total;
        }
      })
    },
    getMoney(){
      getShopAsset(this.shop.shopId).then(res=>{
        console.log('默认数据',res)
        if(res.code == 200){
          this.form = res.data;
        }
      })
    },
    reset(){},
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val
      this.getData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val
      this.getData();
    },
    change(){
      this.$forceUpdate()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
.cardHead{
  font-size: 20px;
  font-weight: bold;
}
.ii{
  margin-left: 5px;;
}
.cardContent{
  margin-top: 20px;
  height: 40px;
  line-height: 40px;
}
.wfs{
  font-size: 20px;
}
.b6{
  width: 60%
}
.b4{
  width: 40%
}
</style>